 (function() {
   'use strict';

   angular
     .module('ctnv2')
     .controller('TokenController', TokenController);

   /** @ngInject */
   function TokenController($scope, $state, Auth ){

     var vm = this;
     vm.validateToken = validateToken;

     function validateToken() {
        vm.dataLoading = true;
        Auth.getCenter(vm.center).then(
            function (response) {
                Auth.center = response.data;
                vm.dataLoading = false;
                $state.go("signup");
            },
            function errorCallback(response) {
                alert("bad token");
                vm.dataLoading = false;
            }
        );
     }

   }
 })();

(function() {
  'use strict';

  angular
    .module('ctnv2')
    .controller('SignupController', SignupController);

  /** @ngInject */
  function SignupController($scope, $state, Auth){

    var vm = this;
    vm.user.center = Auth.center;

    //check valid token here:
    if(!vm.user.center) {
        $state.go("token");
    }

    vm.register = function (user) {
      Auth.userRegister(user).then(
        function (response) {
            //user existence checking
            if(response.data.id==null) {
            alert("user existed, please change your username");
            }
            else {
                localStorageService.set('user', response.data);
                $state.go("demo");
            }
        });
    }

  }
})();

